export default {
	props:["pid"],
	data() {
		return {
			info: []
		}
	},
	methods: {
		getInfo(){
			uni.request({
				url:"https://api.bailiban.com/api/admin/miniprogram/project/info?id="+this.pid,
				header:{
					"Authorization":uni.getStorageSync("blbUI-token")
				},
				method:"GET",
				dataType:"json",
				success:(res)=> {
					console.log(res)
					if(res.data.status == 200){
						this.info = res.data.data
					}else{
						uni.showModal({
							content: res.data.message,
							showCancel: false,
							success: function (res) {
								if (res.confirm) {
									uni.navigateBack({
										delta: 1
									});
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						})
					}
				},
				fail() {
					uni.showToast({
						title:'请求接口失败'
					})
				}
			});
		},
		jionProject(){
			if(this.info.project.projectPrice != 0){
				uni.showModal({
					title:"确认购买",
					content:"本项目为收费项目，价格为"+this.info.project.projectPrice+",是否需要购买学习",
					success: function (res) {
						if (res.confirm) {
							uni.showToast({
								title:'不支持购买'
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
			}else{
				uni.request({
					url:"https://api.bailiban.com/api/admin/miniprogram/my/jionProject/"+this.pid,
					header:{
						"Authorization":uni.getStorageSync("blbUI-token")
					},
					method:"POST",
					dataType:"json",
					success:(res)=> {
						console.log(res)
						if(res.data.status == 200){
							
						}else{
							uni.showModal({
								content: res.data.message,
								showCancel: false,
								success: function (res) {
									if (res.confirm) {
										console.log('用户点击确认');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							})
						}
					},
					fail() {
						uni.showToast({
							title:'请求接口失败'
						})
					}
				});
			}
		}
	},
	//组件调用后
	created(){
		this.getInfo()
	}
}
